    <div class="mark-title">
      <span>
        مارک دستگاهت چیه؟ <span class="tip-star">*</span>
      </span>
    </div>
    <div class="box">
      <div class="box-item">
        <btn @click="setDeviceMark('Bootan')">بوتان</btn>
      </div>
      <div class="box-item">
        <btn @click="setDeviceMark('Tachi')">تاچی</btn>
      </div>
      <div class="box-item">
