</template>

<script>
import RadioBox from "@/components/RadioBox";
import router from "@/router";

export default {
  name: "deviceMark",
  data() {
    return {
      MySelectedValue: "",
      mark: "",
    }
  },
  components: {
    RadioBox,
  },
  methods: {
    Action() {
      this.$router.push('end')
    },
    setDeviceMark(mark) {
      this.$store.commit('setDeviceMark', mark)
      mark === 'etc' ? this.$router.push('deviceModel') : this.$router.push('packageIssue')
    }
  }
}
</script>

